import { useAtom } from "jotai"
import { Link, useNavigate } from "react-router-dom"
import { MOBILE_SCROLL_THRESHOLD } from "@rp/common/settings"
import { BigdataLogoWhite } from "@rp/react/common/assets"
import { BaseMobileHeader, HideOnScroll } from "@rp/react/web/common/base-components"
import { ShareDocument } from "@rp/react/web/common/composite-components"
import { Box, IconButton } from "@rp/react/web/mui/core"
import { ArrowBack } from "@rp/react/web/mui/icons"
import { Search } from "@rp/react/web/mui/icons"
import { HOME_ROUTE } from "@rp/react/web/router"
import { atomOpenSearchBar } from "../../atoms/open-search-bar"

type Props = {
  showSuggestionBar: boolean
  isOpenDocument?: boolean
}

const HomeHeaderMobile = ({ showSuggestionBar, isOpenDocument = false }: Props) => {
  const navigate = useNavigate()
  const [, setIsOpen] = useAtom(atomOpenSearchBar)

  const showBackArrow = isOpenDocument

  return (
    <HideOnScroll threshold={MOBILE_SCROLL_THRESHOLD} sticky={isOpenDocument}>
      <BaseMobileHeader
        leftColumns={2}
        centerColumns={8}
        rightColumns={2}
        leftContent={
          showSuggestionBar ? (
            showBackArrow ? (
              <IconButton data-testid="back-arrow-button" onClick={() => navigate(-1)} size="small">
                <ArrowBack />
              </IconButton>
            ) : (
              <IconButton onClick={() => setIsOpen(true)}>
                <Search />
              </IconButton>
            )
          ) : null
        }
        centerContent={
          showSuggestionBar ? (
            <Box
              sx={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                gap: 1,
              }}
            >
              <Link
                to={HOME_ROUTE}
                onClick={() => {
                  window.scroll({ top: 0, behavior: "smooth" })
                }}
                style={{ alignItems: "center", display: "flex" }}
              >
                <BigdataLogoWhite title="bigdata-logo" width={95} />
              </Link>
            </Box>
          ) : null
        }
        rightContent={isOpenDocument ? <ShareDocument /> : null}
      />
    </HideOnScroll>
  )
}

export { HomeHeaderMobile }
